import WebFont from 'webfontloader';

WebFont.load({
google: {
  families: ['Poppins:300,400,500,600,700,800,900']
},
active: function() {
  // Fonty jsou nateny a pipraveny k pouit
  document.documentElement.classList.add('fonts-loaded');
},
inactive: function() {
  // Fonty se nepodailo nast
  console.warn('Fonty se nepodailo nast.');
}
});
